import { Component, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { ActivatedRoute, Router } from '@angular/router';
import {
  AppNameService,
  EventbusService, MainBarTitleService, MakeSubscriptionCoreComponent, scaleSmooth
} from '@front-shared-lib';
import { navByBus } from '@main-shared-lib';

@Component({
  selector: 'app-subscribe',
  standalone: true,
  imports: [
    MakeSubscriptionCoreComponent
  ],
  animations: [scaleSmooth],
  templateUrl: './subscribe.component.html',
  styleUrl: './subscribe.component.scss'
})
export class SubscribeComponent
implements OnInit {
  makingSubscription = false;
  companyId: string;

  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;


  constructor(
    private eventbus: EventbusService,
    private route: ActivatedRoute,
    private router: Router,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) {
    this.companyId = this.route.snapshot?.params['id'];
  }

  ngOnInit(): void {
    this.mainBarTitle.title = 'Subscribe to Plans - Manage Subscription Plans';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: 'Easily manage and subscribe to your plans for tailored service options.'
    });
  }
}
